
import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/index.js'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  
  {
    // 登录视图
    path: '/routerPage/',
    name: 'Login',
    component: () => import('../views/routerPage/Login.vue')
  },
  {
    // 注册视图
    path: '/routerPage/',
    name: 'Register',
    component: () => import('../views/routerPage/Register.vue')
  },
  {
    // 用户基本信息后台管理
    path: '/admin/',
    name: 'UserAdmin',
    component: () => import('../views/admin/User.vue')
  },
  {
    //Markdown输入
    path:'/note/',
    name:'Markdown',
    component: () => import('../views/note/Markdown.vue')
  },
  {
    //Notetxt输入
    path:'/note/',
    name:'Notetxt',
    component: () => import('../views/note/Notetxt.vue')
  },
  {
    //NoteECY输入
    path:'/note/',
    name:'NoteECY',
    component: () => import('../views/note/NoteECY.vue')
  },
  {
    //Video页面
    path:'/note/',
    name:'VideoPlayer',
    component: () => import('../views/note/VideoPlayer.vue')
  },
  {
    //文件管理
    path:'/fileManage/',
    name:'fileManage',
    component: () => import('../views/fileManage/File.vue')
  },
  {
    //文件管理
    path:'/fileManage/',
    name:'tFileManage',
    component: () => import('../views/fileManage/Teamfile.vue')
  },
  {
    //文件管理
    path:'/fileManage/',
    name:'teamManage',
    component: () => import('../views/fileManage/Team.vue')
  },
  
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  if(to.name==="UserAdmin"){
    if(store.state.user.enabled!=2){
      next({name:'Login'})
    }
    else{
      next()
    }  
  }
  else if(store.state.user.enabled==0){
    if(to.name!=='Login'&&to.name!=='Register'&&to.name!=='Home'){
      next({name:'Login'})
    }
    else{
      next()
    }
  }
  else{
    next()
  }
})


export default router
