import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);


const store = new Vuex.Store({
  state: {
    // 代表用户自身
    user: {
      id: null,
      username: 'Visitor',
      password: '',
      enabled: 0,
      //enable属性说明：-1不可用，0未登录，1可用(普通用户)，2管理用户
      email: 'example@qq.com',
      icon: '',
      // 和用户相关的一些链接，比如用户自身的描述
      _links: []
    },
    //控制侧栏隐藏和出现
    drawer: true,
    //控制用户的编辑状态：即，正在编辑未被创建的文件还是编辑已经被创建的文件
    targetIP: "",
    // "http://10.127.212.99"(教学楼)
    //"http://10.21.145.59"（宿舍楼）
    //"http://10.122.215.128"(宿舍2)

    file: {
      //正在编辑文件的内容
      editingFile: "",
      //正在编辑文件的文件名
      editingFileName: "",
      //正在编辑文件属于个人还是团队
      editingFileUserType: "",
      //正在编辑文件的路径
      editingFilePath: "",
      //正在编辑文件的人
      editingFileEditor:"",
    },



  },
  mutations: {
    changeDrawerAvailable(state) {
      state.drawer = !state.drawer;
    },
    // 登录后设置user
    setUser_l(state, User) {
      state.user.email = User.email;
      state.user.username = User.username;
      state.user.enabled = User.enabled;
    },
    setUser_r(state, User) {
      state.user.username = User.username;
    },
    // 注销后清除user
    removeUser(state) {
      state.user = {
        id: null,
        username: 'Visitor',
        password: '',
        enabled: 0,
        email: 'example@qq.com',
        icon: '',
        // 和用户相关的一些链接，比如用户自身的描述
        _links: []
      };
      state.drawer = true;

      state.file= {
        //正在编辑文件的内容
        editingFile: "",
        //正在编辑文件的文件名
        editingFileName: "",
        //正在编辑文件属于个人还是团队
        editingFileUserType: "",
        //正在编辑文件的路径
        editingFilePath: "",
        editingFileEditor:"",
      };

    },
    setEditingFile(state,value){
      state.file.editingFile = value;
    },
    setEditingFileInformation(state, file) {
      state.file.editingFile = file.fileData;
      state.file.editingFileName = file.fileName;
      state.file.editingFileUserType = file.fileType;
      state.file.editingFilePath = file.filePath;
      state.file.editingFileEditor=file.editor;
    },


  },
  actions: {},
  modules: {}
})
export default store;